$font-ptrootuiweb: "PTRootUIWeb", "Arial", sans-serif;

:root {
  --basic-white: #ffffff;
  --basic-black: #000000;
  --basic-grey: #efefef;
  --basic-red: #d70e2a;
  --black-bg: #171717;
  --black-10: rgba(0, 0, 0, 0.1);
  --black-30: rgba(0, 0, 0, 0.3);
  --black-70: rgba(0, 0, 0, 0.7);
}

$border-black-10: 1px solid var(--black-10);
$border-black-30: 1px solid var(--black-30);

$tablet-width: 720px;
$desktop-width: 1000px;
